@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald&family=Quicksand&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
* {
    margin: 0px;
}

body {
    background: #f5f5f5;
    margin: 0px;
    padding: 0px;
    font-family: 'Quicksand';
}

.header {
    font-family: 'Oswald';
    color: #f5f5f5;
    background-color: #03396c;
    padding: 0.5em;
    border-bottom: 0.25em solid #3b0056;
}

.text {
    padding: 0.5em;
}

.uploadInput {
    width: 0px;
    height: 0px;
    visibility: hidden;
    opacity: 0;
}

.ctrlBtn {
    font-family: 'Quicksand';
    margin: 0.5em;
    padding: 8px 10px;
    font-size: 16px;
    background: #478a28;
    color: #f5f5f5;
    display: inline-block;
    border: 0;
    border-radius: 0.5em;
    cursor: pointer;
    outline: none;
}

.image {
    max-width: 90%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 5px;
    margin-bottom: 5px;
    -webkit-flex: 0 0 60%;
            flex: 0 0 60%;
    border: 0.15em solid #000000;
    border-radius: 0.25em;
}

.logoCPU {
    width: 42px; 
    height: 42px;
    padding-right: 10px;
    margin-bottom: -7px;
}
